<template>
  <v-container fluid fill-height>
    <v-content>
      <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
          <v-card elevation="8" raised>
            <v-toolbar dark color="primary">
              <v-img contain :src="require('../assets/logo-white-honda.png')" />
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-card-text>
              <v-form>
                <v-text-field prepend-icon="business" id="txtCodEmpresa" placeholder="1012460" maxlength="10" name="codEmpresa"
                  v-model="usuario.codEmpresa" label="Empresa" type="text" />
                <v-text-field prepend-icon="person" id="txtCodUsuario" placeholder="ADILSON" maxlength="10" name="codUsuario"
                  v-model="usuario.codUsuario" label="Usurio" type="text" />
                <v-text-field prepend-icon="lock" id="txtSenha" placeholder="teste1" name="senha"
                  v-model="senhaDigitada" label="Senha" type="password"/>
              </v-form>
            </v-card-text>
            <v-card-actions>
              <v-spacer/>
              <v-btn color="primary" label="Login" @click="clickEntrar" :disabled="isLoginDesabilitado">Login</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-content>
  </v-container>
</template>

<script>
import { isEmpty } from 'lodash'
import { createNamespacedHelpers } from 'vuex'
import util from '../utils'

const { mapActions } = createNamespacedHelpers(util.moduleNames.LOGIN)

export default {
  name: 'Login',
  data () {
    return {
      usuario: {
        codEmpresa: '',
        codUsuario: '',
        senha: ''
      },
      senhaDigitada: ''
    }
  },
  methods: {
    ...mapActions(['setDadosSessao', 'login']),

    clickEntrar () {
      this.usuario.senha = btoa(this.senhaDigitada)
      const usuario = this.usuario
      this.login(usuario.codEmpresa, usuario.codUsuario, usuario.senha)
      this.setDadosSessao(usuario)
      this.$router.push(util.routes.MENU.path)
    }
  },
  computed: {
    isLoginDesabilitado () {
      const usuario = this.usuario
      return (isEmpty(usuario.codEmpresa) || isEmpty(usuario.codUsuario) || isEmpty(this.senhaDigitada))
    }
  }
}
</script>
