<template>
  <v-card class="text-xs-center ma-2 rounded-card" elevation="2">
    <v-img contain :src="imagemModelo" @error="onImageError" class="mb-2" />
    <v-card-title primary-title>
      <div>
        <h3 class="subheading font-weight-black">{{modelo.codModelo}}</h3>
      </div>
    </v-card-title>
  </v-card>
</template>

<script>
export default {
  name: 'ItemCatalogo',
  props: {
    modelo: Object
  },
  data () {
    let img = null
    try {
      img = require('../assets/modelos/miniatura_' + this.modelo.idModelo + '.png')
    } catch (e) {
      img = require('../assets/image-not-found.png')
    }

    return {
      imagemModelo: img
    }
  },
  methods: {
    onImageError (target) {
      console.log('Erro ao carregar img')
      // TODO: se necessrio :p
    }
  }
}
</script>

<style lang="stylus" scoped>
.rounded-card {
  border-radius: 8px;
}
.rounded-card:hover {
	-webkit-box-shadow: 0px 5px 5px -3px rgba(0,0,0,0.2), 0px 8px 10px 1px rgba(0,0,0,0.14), 0px 3px 14px 2px rgba(0,0,0,0.12) !important;
	box-shadow: 0px 5px 5px -3px rgba(0,0,0,0.2), 0px 8px 10px 1px rgba(0,0,0,0.14), 0px 3px 14px 2px rgba(0,0,0,0.12) !important;
}
</style>
