export const setMsgRetornoApi = (context, error, tipo, contentType) => {
  let response = error.response
  if (response) {
    if (contentType === 'ArrayBuffer') {
      var decodedString = decodeURIComponent(escape(String.fromCharCode.apply(null, new Uint8Array(response.data))))
      response.data = JSON.parse(decodedString)
    }

    /* const mensagem = {
      codigo: response.data.codigo,
      detalhe: response.data.detalhe,
      tipo: response.data.tipo,
      stackTrace: response.data.stackTrace,
      codigoHttp: response.status
    }

    context.dispatch(ACTION_DESLIGARLOADING)
    context.dispatch(ACTION_MSGRETORNO, mensagem) */
  }
}
